import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from '@helix/platform/shared/api';

@Injectable({
  providedIn: 'root'
})
export class RxViewActionDefinitionAdapterRegistryService {
  private runtimeAdapters: Map<string, IDefinitionAdapter> = new Map();
  private designAdapters: Map<string, IDefinitionAdapter> = new Map();

  registerRuntimeAdapter(actionName: string, adapter: IDefinitionAdapter): void {
    this.runtimeAdapters.set(actionName, adapter);
  }

  registerDesignAdapter(actionName: string, adapter: IDefinitionAdapter): void {
    this.designAdapters.set(actionName, adapter);
  }

  getRuntimeAdapter(actionName: string): IDefinitionAdapter {
    return this.runtimeAdapters.get(actionName);
  }

  getDesignAdapter(actionName: string): IDefinitionAdapter {
    return this.designAdapters.get(actionName);
  }
}
